import { useContext, useEffect, useMemo, useState } from "react";
import FormUtil from "../../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../../utils/form/validateUtil";
import TreeUtil from "../../../../../utils/treeUtil";
import { GlobalContext } from "../../../../entry/systemEntry";
import { FactoryTreeContext } from "../../logicMainteTab";
import LogicUtil from "../../util/logicUtil";
import ModifyOperations from "../common/modifyOperations";
import LogicTemplateDialog from "../../dialog/logicTemplateDialog";
import LogicElementUtil from "../../util/logicElementUtil";

const EditorSampleItem = () => {
    const { store, setStore } = useContext(GlobalContext);
    const { removeSelf, node, rootData, invalidate, setModify, isModify } = useContext(FactoryTreeContext);
    const wrap = node.data as LogicUtil.WrapElement;

    const [name, setName] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });

    const mappingForm = () => {
        const param = wrap.data as LogicUtil.NodeSeedItem;
        setName({ ...name, value: param.name });
    };

    /**  */
    // const sampleNameList = useMemo(() => {
    //     const mng = (node.parent?.data as LogicUtil.WrapElement).data;
    //     return LogicElementUtil.getSeedNameList(mng);
    // }, [node]);

    useEffect(() => {
        if (wrap.data == null) {
            setModify(true);
        } else {
            mappingForm();
        }
    }, [node]);


    return (<>
        <ModifyOperations.Component
            mappingForm={mappingForm}
            checkableFields={[name]}
            firstRegist={() => {
                const element: LogicUtil.NodeSampleItem = {
                    name: name.value,
                    params: []
                };
                wrap.data = element;

                invalidate();
                setStore({ ...store });
            }}
            modifyRegist={() => {
                const param = wrap.data as LogicUtil.NodeLogicItem;
                param.name = name.value;
                wrap.data = param;
            }}
        />
        <FormUtil.BorderPanel
            title="param"
            innerJsx={<>
                <FormUtil.FormRecord
                    titleLabel="Name"
                    jsx={
                        <FormUtil.TextField
                            width={200}
                            isReadOnly={!isModify}
                            checkable={name}
                            setCheckable={setName}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                },
                                {
                                    checker: (value) => ValidateUtil.checkStringLength(value, 1, 16),
                                    errorType: "length"
                                },
                                {
                                    checker: (value) => LogicUtil.checkFieldName(value),
                                    errorType: "value"
                                },
                                // {
                                //     checker: (value) => {
                                //         const list = sampleNameList.filter(name => {
                                //             // 
                                //             if (wrap.data != null) {
                                //                 return (wrap.data as LogicUtil.NodeLogicItem).name !== name;
                                //             }
                                //             return true;
                                //         });
                                //         return !list.includes(value);
                                //     },
                                //     errorType: "relation"
                                // }
                            ]}
                        />
                    }
                />
            </>}
        />
        <FormUtil.BorderPanel
            title="operation"
            isEnable={!isModify}
            innerJsx={<>
                <FormUtil.ButtonRecord
                    isCenter
                    buttons={[
                        {
                            label: 'Delete',
                            callback: () => {
                                removeSelf();
                            }
                        }
                    ]}
                />
                {/*  */}
                {LogicUtil.getSwapButtons(node, invalidate)}
            </>}
        />
    </>);
}

export default EditorSampleItem;